import React, { useState, useEffect } from "react";
import { Redirect } from "react-router";
import {
  Container,
  Row,
  Col,
  CardBody,
  Form,
  FormGroup,
  Input,
  Label,
  Button,
} from "reactstrap";
// import { ToastContainer, toast } from "react-toastify";
// import axios from 'axios';

// Register  Auth rename  Register Signin !
//   !    ! 
import KakaoAuth from "../../components/account/kakaoAuth";
import NaverAuth from "../../components/account/naverAuth";

function Auth(props) {
  const toggleform = () => {
    document.querySelector(".cont").classList.toggle("s--signup");
  };
  
  //  ,  useState   .
  //  user_id, user_pwd !
  const [loginId, setLoginId] = useState("");
  const [loginPassword, setLoginPassword] = useState("");

  const Signin = (event) => {
    event.preventDefault();
    console.log(" :", loginId);
    console.log(" :", loginPassword);
    // fetch(`${process.env.PUBLIC_URL}/account/signinjwt`, {
    fetch(`${process.env.REACT_APP_API_URL}/account/signinjwt`, {
      method: 'POST',
      headers:{
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        userId: loginId,
        userPwd: loginPassword
      })
    })
    .then(res => res.json())
    .then(res => {
      console.info("Signin   JWT :", res)
      console.info("Signin   JWT :", res.token)
      //       
      localStorage.setItem('access-token', res["token"])
      // <Redirect to="http://localhost:3000/#/"/>
      console.log(": ", localStorage.getItem('access-token'))
      fetch(`${process.env.REACT_APP_API_URL}/account/userinfo`, {
        headers: {
          token: localStorage.getItem('access-token')
        }
      })
      .then(res => res.json())
      .then(res =>
        ( res.storeId !== null ) ? ( window.location.href = '/storeadmin' ) : ( window.location.href = '/profile' )        
      )  
    })
    .catch(error =>
      alert("  !")
    )
  };

  //  
  // useState   form     . 
  const [id, setId] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkid, setCheckid] = useState(false);
  
  //   user_id, user_name  !
  const Signup = (event) => {
    event.preventDefault();
    if (checkid === false) {
      alert("  !")
    // } else if ( name !== "" && phone !== "" && email !== "") {
    } else if (Boolean(name) === false || Boolean(phone) === false || Boolean(email) === false) {
      alert("  !")
    } else { 
      // fetch(`${process.env.PUBLIC_URL}/account/signup`, {
      fetch(`${process.env.REACT_APP_API_URL}/account/signup`, {
        method: "POST",
        headers:{
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          userId: id,
          userName: name,
          userPhone: phone,
          userEmail: email,
          userPwd: password
          // DATE  ! 
          // userDate: "2021-11-11",
        })
      })
      // .then(res => res.json())
      .then(res => {
        //   !   httpOK  .   if  ! 
        console.log("Signup :", res)
        // res NULL badrequest     
        //  OK  :       JWT !   
        if (res.status === 200 || res.status === 201) {
          // fetch(`${process.env.PUBLIC_URL}/account/signinjwt`), {
          fetch(`${process.env.REACT_APP_API_URL}/account/signinjwt`, {
            method: "POST",
            headers:{
              'Content-Type': 'application/json'
            },   
            body: JSON.stringify({
              userId: id,
              userPwd: password,
            })
          })
          .then(res => res.json())
          .then(res => {
            console.info("Signup     :", res)
            localStorage.setItem('access-token', res.token)
          })
        } else {
          //   ,   ?   
          console.error("  :", res)
        }
      })
    }
    //     ? 
    if (Boolean(localStorage.getItem('access-token')) == true && localStorage.getItem('access-token') != "undefined") {

      //storeId ?
      fetch(`${process.env.REACT_APP_API_URL}/account/userinfo`, {
        headers: {
          token: localStorage.getItem('access-token')
        }
      })
      .then(res => res.json())
      .then(res =>
        ( res.storeId !== null ) ? ( window.location.href = '/storeadmin' ) : ( window.location.href = '/profile' )        
      )  
    }
  };

  const Checkid = () => {
    // (id == "") ? alert(" ") : Checkid()}
    fetch(`${process.env.REACT_APP_API_URL}/account/userdupli`, {
      method: "POST",
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        userId: id,
      })
    })
    .then(res => {
      if (res.status === 200) {
        // console.log(":", res),
        alert("  ")
        setCheckid(true)
      } else {
        // console.log(" :", res),
        alert("  ")
      }
    })
  };

  return (
    <div className="page-wrapper">
      <Container fluid={true} className="p-0">
        {/* <!-- login page start--> */}
        <div className="authentication-main">
          <Row>
            <Col md="12">
              <div className="auth-innerright">
                <div className="authentication-box">
                  <CardBody>
                    <div className="cont text-center !s--signup">
                      <div>
                        <Form className="theme-form">
                          <h4></h4>
                          {/* <h6>   </h6> */}
                          <FormGroup>
                            <Label className="col-form-label pt-0">
                              
                            </Label>
                            <Input
                              className="form-control"
                              type="text"
                              name="loginid"
                              value={loginId}
                              onChange={(e) => setLoginId(e.target.value)}
                              placeholder=" "
                              //  !
                              required
                              // className="btn-pill"
                            />
                          </FormGroup>
                          <FormGroup>
                            <Label className="col-form-label"></Label>
                            <Input
                              className="form-control"
                              type="password"
                              name="password"
                              value={loginPassword}
                              onChange={(e) => setLoginPassword(e.target.value)}
                              placeholder=" "
                              required
                              // className="btn-pill"
                            />
                          </FormGroup>
                          <div className="checkbox p-0">
                            <Input id="checkbox1" type="checkbox" />
                            <Label for="checkbox1"> </Label>
                          </div>
                          <FormGroup className="form-row mt-3 mb-0">
                            <Button 
                              color="warning btn-block"
                              onClick={(event) => Signin(event)}
                            >
                              
                            </Button>
                          </FormGroup>
                          <div className="login-divider"></div>
                          <div className="social mt-3">
                            <Row form className="btn-showcase">
                              <Col md="6" sm="6">
                                <KakaoAuth />
                                {/* <Button
                                  color="social-btn btn-kakao"
                                  // social-btn _forms.scss   !
                                  // onClick={facebookAuth}
                                >
                                   
                                </Button> */}
                              </Col>
                              <Col md="6" sm="6">
                                {/* <Button
                                  color="social-btn btn-naver"
                                  // onClick={twitterAuth}
                                >
                                </Button> */}
                                <NaverAuth />
                              </Col>
                            </Row>
                          </div>
                        </Form>
                      </div>
                      <div className="sub-cont">
                        <div className="img">
                          <div className="img__text m--up">
                            <h2> </h2>
                            <p>
                                  
                            </p>
                          </div>
                          <div className="img__text m--in">
                            <h2> </h2>
                            <p>
                                   
                            </p>
                          </div>
                          <div className="img__btn" onClick={toggleform}>
                            <span className="m--up"></span>
                            <span className="m--in"></span>
                          </div>
                        </div>
                        <div>
                          <Form className="theme-form">
                            <h4 className="text-center"></h4>
                            {/* <h6 className="text-center">
                              
                            </h6> */}
                            <Row form>
                              <Col md="9">
                                <FormGroup>
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="id"
                                    value={id}
                                    onChange={(e) => {setId(e.target.value); setCheckid(false);}
                                    }
                                    placeholder=" "
                                    required
                                  />
                                </FormGroup>
                              </Col>
                              <Col md="3">
                                <Button
                                  // setId   id  ""      
                                  onClick={() => 
                                    (id == "") ? alert(" ") : Checkid()}
                                >
                                  
                                </Button>
                              </Col>
                              <Col md="12">
                                <FormGroup>
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="name"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                    placeholder=" "
                                    required
                                  />
                                </FormGroup>
                              </Col>
                              <Col md="12">
                                <FormGroup>
                                  <Input
                                    className="form-control"
                                    type="text"
                                    name="phone"
                                    value={phone}
                                    onChange={(e) => setPhone(e.target.value)}
                                    placeholder=" "
                                    required
                                  />
                                </FormGroup>
                              </Col>
                            </Row>
                            <FormGroup>
                              <Input
                                className="form-control"
                                type="email"
                                name="email"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                placeholder=" "
                                required
                              />
                            </FormGroup>
                            <FormGroup>
                              <Input
                                className="form-control"
                                type="password"
                                name="password"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                placeholder=" "
                                required
                              />
                            </FormGroup>
                            <FormGroup className="form-row mt-3 mb-0">
                              <Button color="primary btn-block"
                                color="warning btn-block"
                                onClick={(event) => {Signup(event)}}                              
                              > 
                              
                              </Button>
                            </FormGroup>
                            {/* <Row form>
                              <Col sm="8">
                                <div className="text-left mt-2 m-l-20">
                                  Are you already user?
                                  <a
                                    className="btn-link text-capitalize"
                                    href="login.html"
                                  >
                                    Login
                                  </a>
                                </div>
                              </Col>
                            </Row> */}
                            <div className="form-divider"></div>
                            <div className="social mt-3">
                            <Row form className="btn-showcase">
                              <Col md="6" sm="6">
                                <KakaoAuth />
                                {/* <Button
                                  color="social-btn btn-kakao"
                                  // social-btn _forms.scss   !
                                  // onClick={facebookAuth}
                                >
                                   
                                </Button> */}
                              </Col>
                              <Col md="6" sm="6">
                                <NaverAuth />
                              </Col>
                            </Row>
                          </div>
                          </Form>
                        </div>
                      </div>
                    </div>
                  </CardBody>
                </div>
              </div>
            </Col>
          </Row>
        </div>
        {/* <!-- login page end--> */}
      </Container>
    </div>
  );
};

export default Auth;
