import React, { useState, useEffect } from "react";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from "reactstrap";
// import happynew from "../../assets/images/happynew.png"


function Usertotal() {
    const [userTotal, setUserTotal] = useState(0);
  
    useEffect(()=>{
        fetch(`${process.env.REACT_APP_API_URL}/main/usertotal`)
        .then((res) => res.json())
        .then((total) => {
          setUserTotal(total);
        });
    }, [])
  
    return(
      <div className="usertotal carouselItem">
        <br/>
        <h3 className="usertotaltotal">    { userTotal } .</h3>
        <br/>
        <h4 className="usertotaltotal">    !</h4>
      </div>
    );
}

export default Usertotal;