import React, { Fragment, useState, useEffect } from "react";
import {
  VerticalTimeline,
  // VerticalTimelineElement,
} from "react-vertical-timeline-component";
import 'react-vertical-timeline-component/style.min.css';
// import { Edit, Video, Image, Activity } from "react-feather";
import TimelineItem from "./timelineItem";
// import Contribution from "../support/contribution";
// import { Col, Container, Row } from "reactstrap";

function Timeline() {
  const [contributions, setContributions] = useState([]);

  //    ! ^0^
  useEffect(() => {
    fetch(`${process.env.REACT_APP_API_URL}/account/usercontribution`, {
      // fetch   GET  method  .
      // header: localStorage.getItem('access-token')
      headers: {
        //  "token"  ?  authentication.js  fetch  
        //     'access_token' ?   '-' .
        //  fetch     memberInfo, memberQnA   !
        token: localStorage.getItem('access-token')
      }
    })
    .then(res => res.json())
    .then(res => {
      setContributions(res);
      console.log("  :", res)
      console.log("  :", contributions)
      console.log(" ?:", contributions[0])
    });
  }, []);
  
  // return (
  //   <div></div>
  // );
  if (contributions.length !== 0) {
    // console.log(" 0 :", contributions)
    return (
      <Fragment>
        <VerticalTimeline
          layout={"1-colum-right"}
        >
          {contributions.map((contributionItem, index) => 
            <TimelineItem
              contribution = {contributionItem}
            />
          )}
        </VerticalTimeline>
      </Fragment>
    );
  } else {
    return (
      <div pt-20 >
       <p>     !</p>
       <p>1.    </p>
       <p>2.   </p>
       <p>3.     </p>
       <p>4.    !</p>
       <p>5.    ,   .</p>
      </div>
    );
  }
};

export default Timeline;
